import React, {useState} from 'react'
import './SearchBox.css'
import SearchIcon from '@material-ui/icons/Search';
import MicIcon from '@material-ui/icons/Mic'
import { Button } from '@material-ui/core';
import { useHistory } from 'react-router-dom';
import {useStateValue} from '../StateProvider'
import { actionTypes } from '../reducer';
function SearchBox({hideButtons = false}) {
 const [state , dispatch] = useStateValue();
    const [input , setInput] = useState('');
  const history =   useHistory()

    const search = (e) => {
        e.preventDefault()
        // console.log("You Hit The Submit " ,  input)
        dispatch({
          type: actionTypes.SET_SEARCH_TERM,
          term:input
        })
        history.push('./search');
        // console.log(state)
    }
    return (
        <form  className = "searchbox">
            <div className = "search-input" >
             <SearchIcon className="search-icon" />
             <input placeholder="Search Google or type a URL"value ={input} onChange={e => setInput(e.target.value)} />
             <MicIcon className="microphone" title="search by voice"/>
            </div>
            {!hideButtons
            ?

            (<div className ="search-buttons">
             <Button type="submit" onClick ={search}>Google Search</Button>
              <Button variant= "outlined" >I'm Feeling Lucky</Button>
            </div>
            )
    :
       (
        <div className ="search-buttons">
        <Button  className ="hide-me" type="submit" onClick ={search}>Google Search</Button>
         <Button variant= "outlined" className ="hide-me">I'm Feeling Lucky</Button>
       </div>
      
       )
}
        </form>
    )
}

export default SearchBox
