import React  from 'react';
import { useStateValue } from '../StateProvider';
import useGoogleSearch from '../useGoogleSearch';
import './Search.css';
// import Response from './response'
import {Link} from "react-router-dom"
import SearchBox from '../components/SearchBox';
import SearchIcon from '@material-ui/icons/Search';
import  LocalOfferIcon from '@material-ui/icons/LocalOffer';
import  RoomIcon from '@material-ui/icons/Room'
import  DescriptionIcon from '@material-ui/icons/Description'
import  ImageIcon from '@material-ui/icons/Image'
import  MoreVertIcon from '@material-ui/icons/MoreVert'


function Search(){
    const [{term } , dispatch] = useStateValue();
    const {data} = useGoogleSearch(term)
  
    
    return (
        <div className ="search-page">
       <div className ="searchpage-header">
         <Link to ="/">
         <img className="searchpage-logo"
         src={require("../images/google.png")} alt="..."/>
         </Link>
         <div className="searchpage-headerBody">
              <SearchBox hideButtons/>
              <div className="searchpage-options"> 
              <div className="searchOptionLeft">
                <div className="search-option">
                <SearchIcon />
               <Link to = "/all">All</Link>   
                </div>
                <div className="search-option">
                   <DescriptionIcon />
                   <Link to ="/news">News</Link>
                </div>
                <div className="search-option">
                  <ImageIcon />
                  <Link to='/images'>Images</Link>
                </div>
                <div className="search-option">
                  <LocalOfferIcon />
                  <Link to="/shopping">shopping</Link>
                </div>
                <div className="search-option">
                  <RoomIcon />
                  <Link to ="/maps">maps</Link>
                </div>
                <div className="search-option">
                  <MoreVertIcon />
                  <Link to ="more">more</Link>
                </div>
              </div>
               <div className="search-option-right">
                 <div className="search-option">
                   <Link to="/settings">
                     Settings
                   </Link>
                 </div>
                 <div className="search-option">
                   <Link to="/settings">
                   Tools
                   </Link>
                 </div>
               </div>
              </div>
         </div>
       </div>
       {term && (
       <div className="searchpage-results">
         <p className="searchpage-resultCount">
           About {data?.searchInformation.formattedTotalResults} results ({data?.searchInformation.formattedSearchTime}  seconds) for {term}
         </p>
         {data?.items.map(item => {
           return(
             <div key={item.snippet} className = "searchpage-result">
             <a href={item.link}>
               {item.pagemapcse_image?.length > 0 && item.pagemapcse_image[0].src && (
                 <img className="searchpage-result-image"
                 src={
                   item.pagemapcse_image.length > 0 &&item.pagemapcse_image[0]?.src
                  } 
                 alt="thumb" />
               )}
               {item.displayLink}</a> 
           <a className="searchpage-result-title" href={item.link}>
             <h2>{item.title}</h2>
             </a>
           <p className="searchpage-result-snippet">{item.snippet}</p>
             </div>
           )
         })}
       </div>
       )}
        </div>
    )
}


export default Search
